.BasketItem {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
}

.BasketItem .name {
	-ms-flex: 2 1;
	    flex: 2 1;
}

.BasketItem .price {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.BasketItem .count {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.BasketItem .return {
	margin-left: auto;
	color: #f06595;
	cursor: pointer;
}

.BasketItem .return:hover {
	text-decoration: underline;
}

.BasketItem + .BasketItem {
	margin-top: 1rem;
}



